.welcome {
  overflow: hidden;
  background-attachment: fixed;
  background-size: cover;
  line-height: 1.8;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
}
.welcome--logo {
  width: 10rem;
}

.welcome--about {
  padding: 2rem;
  text-align: justify;
  margin: auto;
}

.welcome__cta-primary {
  background: #e74c3c;
  margin-top: 1rem;
  padding: 0.7rem 1.5rem;
  border: none;
  border-radius: 20px;
  font-weight: 700;
}
.welcome-context {
  z-index: 1;
  width: 100%;
  position: absolute;
  margin: auto;
  top: 25vh;
}

.welcome-context h1 {
  text-align: center;

  font-size: 50px;
}
.welcome-context div {
  text-align: center;
  font-size: 50px;
}
